import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import '../UserTypeSelection/UserTypeSelection.css'; // ✅ 경로 수정
import '../LoginPage.css';

const UserTypeSelection = () => {
  const navigate = useNavigate();
  const [selectedType, setSelectedType] = useState(null);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const handleLogin = () => {
    if (!selectedType) {
      alert('사용자 유형을 선택해주세요.');
      return;
    }
    if (!email || !password) {
      alert('이메일과 비밀번호를 입력해주세요.');
      return;
    }
    // 로그인 처리 로직 추가 (예: API 요청)
    console.log('로그인 시도:', { selectedType, email, password });
  };

  return (
    <div className="user-type-container">
      <h2>회원 유형 선택</h2>
      <div className="user-type-buttons">
        <button
          className={`user-type-button ${
            selectedType === '화주' ? 'selected' : ''
          }`}
          onClick={() => setSelectedType('화주')}
        >
          화주
        </button>
        <button
          className={`user-type-button ${
            selectedType === '포워더' ? 'selected' : ''
          }`}
          onClick={() => setSelectedType('포워더')}
        >
          포워더
        </button>
      </div>

      {/* 로그인 폼 추가 */}
      <div className="login-form">
        <input
          type="email"
          placeholder="이메일"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />
        <input
          type="password"
          placeholder="비밀번호"
          value={password}
          onChang옆에e={(e) => setPassword(e.target.value)}
        />
        <button className="login-button" onClick={handleLogin}>
          로그인
        </button>
      </div>

      {/* 회원가입 버튼 */}
      <p>아직 회원이 아니시라면??, 회원가입 후 이용 부탁드립니다.</p>
      <button className="signup-button" onClick={() => navigate('/signup')}>
        회원가입
      </button>
    </div>
  );
};

export default UserTypeSelection;
